 *{
     margin: 0%;
     padding: 0;
     box-sizing: border-box;
     font-family: sans-serif;
 }

 body{
     width:500px;
     display: flex;
     margin:0 auto;
     background: rgb(2,0,36);
     background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(223,139,3,0.6474964985994398) 50%, rgba(0,212,255,1) 100%);
     
 }

 h1,span{
     color: rgb(49, 56, 61);
 }

 h1{
     margin-bottom: 35px;
 }

 form{
     display: flex;
     flex-direction: column;
     align-items: center;
     height:100vh;
    
 }

 .form-label{
     width: 22px;
     display: flex;
     margin-top: 15px;
     color: rgb(90, 59, 59);
 }

 .form-input{
     padding: 5px 5px;
     border: none;
     margin-bottom: 15px;
     border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
 }
 .form-input:focus{
     outline: none;
 }
 p{
     color: red;
     
 }
 button{
     margin-top: 15px;
    width: 200px;
    padding: 10px;
    background-color: rgb(214, 130, 69);
    margin-bottom: 20px;
    border: none;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
 }
 button:focus{
     outline: none;
 }